
.tab {
  height: 8rem;
}
.tab-ul {
  width: 100%;
  height: 8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #fff;
}
.tab-ul .tab-item {
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 10rem;
  font-size: 1.4rem;
  color: $color-text-ll;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.tab-ul .tab-item .icon-menu {
  width: 4rem;
  height: 4rem;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  position: absolute;
  left: 50%;
  margin-left: -2rem;
}
.tab-ul .tab-item .icon-progress {
  background: url("../../common/image/main.png") no-repeat -86px 0;
}
.tab-ul .tab-item .icon-repayment {
  background: url("../../common/image/main.png") no-repeat 0 0;
}
.tab-ul .tab-item .icon-personal {
  background: url("../../common/image/main.png") no-repeat -42px 0;
}


